.footer{
    background-image: linear-gradient(
    to bottom,
    rgb(62, 192, 39),
    rgb(112, 12, 112));
    display: flex;
    flex-direction: row;
    justify-content: space-around;

&__icon{
    width: 30px;
    height: 69px;
    margin-left: 45px;
    margin-top: 25px;
}
&__icon:hover {
  animation: shake 0.82s;
}
@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }
  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
&__logo{
    width: 80px;
    height: 80px;
    margin: 20px 20px 15px;
    border-radius: 43% 57% 180% 57% / 55% 175% 64% 65%;
    border: solid 5px;
    border-color: #074307;
    }
&__text--link{
  color:#074307;
  font-size: 12px;
  margin-left: 40px;
  margin-top: 40px;
}}